/* Login/Register Styles */
.login {
  max-width: 400px;
  margin: 100px auto;
  padding: 40px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.login h2 {
  text-align: center;
  margin-bottom: 30px;
}

.login form {
  display: flex;
  flex-direction: column;
}

.login form input {
  padding: 12px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
}

.login form button {
  padding: 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

.login form button:hover {
  background-color: #0056b3;
}

.login p {
  text-align: center;
  margin-top: 20px;
}

.login p button {
  background: none;
  border: none;
  color: #007bff;
  cursor: pointer;
  font-size: 16px;
}

.login p button:hover {
  text-decoration: underline;
}

.google-login {
  padding: 12px;
  background-color: #db4437; /* Google Red */
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
  width: 100%; /* Makes the button full-width */
  margin-top: 10px; /* Spacing from the email button */
}

.google-login:hover {
  background-color: #c13527; /* Darker shade for hover effect */
}

/* Store Styles */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
  margin-bottom: 40px;
}

.store-grid {
  display: flex;              /* Use Flexbox */
  flex-wrap: wrap;           /* Allow items to wrap to the next line */
  justify-content: center;    /* Center the items */
  gap: 20px;                 /* Space between items */
}

.product-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  text-align: center;
  background-color: #fff;
  transition: box-shadow 0.3s;
}

.product-card:hover {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.product-image {
  border-radius: 50%; /* This makes the image circular */
  width: 100px; /* Set a fixed width */
  height: 100px; /* Set a fixed height */
  object-fit: cover; /* Ensures the image covers the area */
}

.product-card h3 {
  margin: 10px 0;
  font-size: 1.2em;
}

.product-card p {
  font-size: 1.1em;
  margin-bottom: 15px;
}

button {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #0056b3;
}

/* Cart Summary Styles */
.cart-summary {
  margin-top: 40px;
  border-top: 2px solid #007bff;
  padding-top: 20px;
}

.cart-summary h2 {
  margin-bottom: 20px;
  text-align: center;
}

.cart {
  border: 1px solid #ddd;
  padding: 15px;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.cart-item {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}

.cart-item:last-child {
  border-bottom: none;
}

.cart-total {
  margin-top: 15px;
  text-align: right;
  font-weight: bold;
}

.cart-total p {
  margin-bottom: 10px;
}

.navbar {
  display: flex;
  justify-content: space-around; /* Space links evenly */
  align-items: center;
  background-color: #007bff; /* Bootstrap primary color */
  padding: 15px 0; /* Padding for the navbar */
}

.nav-link {
  color: white; /* Link text color */
  text-decoration: none; /* Remove underline */
  font-size: 18px; /* Font size */
}

.nav-link:hover {
  text-decoration: underline; /* Underline on hover */
  opacity: 0.8; /* Slightly fade on hover */
}

.checkout-container {
  max-width: 600px; /* Limit the width for better readability */
  margin: 50px auto; /* Center the container */
  padding: 20px; /* Add padding */
  background-color: #fff; /* White background for contrast */
  border-radius: 8px; /* Rounded corners */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow */
}

.checkout-container h2 {
  text-align: center; /* Center the header */
  margin-bottom: 30px; /* Spacing below the header */
}

.checkout-item {
  display: flex; /* Use flexbox for alignment */
  justify-content: space-between; /* Space between item name and price */
  padding: 10px 0; /* Spacing above and below each item */
  border-bottom: 1px solid #ddd; /* Light border for separation */
}

.checkout-total {
  display: flex; /* Use flexbox for alignment */
  justify-content: space-between; /* Space between label and total */
  margin-top: 20px; /* Margin above the total section */
  font-weight: bold; /* Bold text for emphasis */
}

.checkout-button {
  margin-top: 20px; /* Space above the button */
  padding: 12px; /* Padding for the button */
  background-color: #007bff; /* Button color */
  color: white; /* Button text color */
  border: none; /* Remove border */
  border-radius: 4px; /* Rounded corners */
  font-size: 18px; /* Font size */
  cursor: pointer; /* Pointer cursor on hover */
  width: 100%; /* Full width */
}

.checkout-button:hover {
  background-color: #0056b3; /* Darker shade for hover */
}

.profile-pic {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
}

.user-profile-container {
  margin: 20px auto; /* Centers the container horizontally with margin */
  max-width: 400px; /* Set a maximum width */
  padding: 20px; /* Padding for better spacing within the container */
  border: 1px solid #ccc; /* Border to define the container */
  border-radius: 8px; /* Rounded corners */
  background-color: #f9f9f9; /* Background color */
  display: flex; /* Optional: Flexbox for better layout control */
  flex-direction: column; /* Stack children vertically */
  align-items: center; /* Center items horizontally */
}

