.user-profile-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

.user-profile-container form {
  display: flex;
  flex-direction: column;
}

.user-profile-container input {
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.user-profile-container button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.user-profile-container button:hover {
  background-color: #0056b3;
}

/* New styles for profile picture */
.profile-picture-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.profile-pic {
  width: 100px; /* Set the desired width for the profile picture */
  height: 100px; /* Set the desired height for the profile picture */
  border-radius: 50%; /* Make it circular */
  cursor: pointer; /* Show pointer cursor */
  object-fit: cover; /* Ensure the image covers the circle */
  border: 2px solid #007bff; /* Optional: border for the profile picture */
}

.logout-button {
  margin-top: 20px;
  padding: 10px;
  background-color: #dc3545; /* Bootstrap danger color */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.logout-button:hover {
  background-color: #c82333; /* Darker red on hover */
}

.discord-message {
  margin-top: 20px; /* Space above the message */
  padding: 10px; /* Padding around the message */
  border: 1px solid #007bff; /* Border color */
  border-radius: 8px; /* Rounded corners */
  background-color: #f1f9ff; /* Light blue background */
  text-align: center; /* Center align the text */
}

.discord-message a {
  color: #007bff; /* Link color */
  font-weight: bold; /* Make the link bold */
}

.discord-message a:hover {
  text-decoration: underline; /* Underline on hover */
}